document.addEventListener('DOMContentLoaded', function () {

    let jsTumbler = document.querySelectorAll('.jsTumbler');

    for (let index = 0; index < jsTumbler.length; index++){
        let item = jsTumbler[index];
        let link = item.querySelectorAll('.jsTumblerLink');
        let body = item.querySelectorAll('.jsTumblerItem');

        initTabs()

        for (let index = 0; index < link.length; index++){
            let thisLink = link[index];
            let thisIndex = thisLink.getAttribute('data-tab');
    
            thisLink.addEventListener('click', function(){
                clear()
                thisLink.classList.add('is-active');
                item.querySelector('.jsTumblerItem[data-tab="'+ thisIndex +'"]').classList.add('is-open');
            })
        }


        function initTabs(){
            initGroup(link);
            initGroup(body);

            function initGroup(group) {
                for (let index = 0; index < group.length; index++){
                    let groupItem = group[index]
                    groupItem.setAttribute('data-tab', index);
                }
            }
        }

        function clear() {
            for (let index = 0; index < link.length; index++){
                let linkItem = link[index]
                linkItem.classList.remove('is-active');
            }
            for (let index = 0; index < body.length; index++){
                let bodyItem = body[index]
                bodyItem.classList.remove('is-open');
            }
        }
    }
})